import React from "react";
import Argument from "./Argument";

import background from "./pics/notebooklong.png";
// import longbackground from "./pics/notebooklong.png";

import cdiag from "./pics/cdiag-removebg-preview.png";
import cbody from "./pics/cbody-removebg-preview.png";
import ccomputer from "./pics/ccomputer-removebg-preview.png";
import cpostit from "./pics/cpostit-removebg-preview.png";
import nobgbaseline from "./pics/nobgbaseline.png";
import nobggeneral from "./pics/nobggeneral.png";
import nobgheadache from "./pics/nobgheadache.png";
import nobgseizures from "./pics/nobgseizures.png";

import "./styles.css";

class Board extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      props: props
    };
  }

  handleDragEnter = (e) => {
    e.preventDefault();
    e.stopPropagation();
  };
  handleDragLeave = (e) => {
    e.preventDefault();
    e.stopPropagation();
  };
  handleDragOver = (e) => {
    e.preventDefault();
    e.stopPropagation();
  };

  render() {
    const noteb = this.props.identity === 99 ? "Notebook" : null;
    var elongate = 0;

    const delBruttoStyle = {
      height: "150px",
      width: "1000px",
      position: "relative",
      backgroundColor:
        this.props.identity === 81
          ? "rgba(255, 0, 0, 0.6)"
          : this.props.identity === 82
          ? "rgba(255, 0, 0, 0.4)"
          : this.props.identity === 83
          ? "rgba(255, 0, 0, 0.2)"
          : null
    };
    if (this.props.identity === 99) {
      const zone99End = this.props.passed_data.data
        .filter((item) => item.zone === 99)
        .map((i) => (
          <Argument key={i.id} item={i} handleChange={this.handleChange} />
        ));
      elongate = zone99End.length;
    }
    const normalStyle = {
      height: elongate > 15 ? "1400px" : elongate>5? "900px":"450px",
      width: "1000px",
      backgroundImage:
        this.props.identity === 99 ? `url("${background}")` : null,
      backgroundSize: "cover",
      position: "relative"
    };

    const ask =
      this.props.identity === 99 && this.props.fill.length < 1
        ? "Drag the clues you want to keep into this virtual notebook"
        : this.props.identity > 50 &&
          this.props.identity !== 99 &&
          this.props.identity !== 81 &&
          this.props.identity !== 82 &&
          this.props.identity !== 83 &&
          this.props.identity !== 76 &&
          this.props.fill.length < 1
        ? "Drag clues onto here to justify your decision and unlock the option below"
        : null;

    return (
      <div
        className={"drag-drop-zone"}
        identity={this.props.identity}
        onDrop={(e) => this.props.handleDrop(e, this.props.identity)}
        onDragOver={(e) => this.handleDragOver(e)}
        onDragEnter={(e) => this.handleDragEnter(e)}
        onDragLeave={(e) => this.handleDragLeave(e)}
        style={
          this.props.identity > 80 && this.props.identity < 90
            ? delBruttoStyle
            : normalStyle
        }
      >
        <h3
          draggable="false"
          style={{
            textAlign: "center",
            paddingTop: "13px",
            fontFamily: "Chalkduster",
            fontSize: "30px",
            color: "black"
          }}
        >
          {noteb}
        </h3>
        <div style={{ paddingLeft: "140px" }}>{this.props.fill}</div>
        <img
          src={
            this.props.identity === 1
              ? nobgbaseline
              : this.props.identity === 2
              ? ccomputer
              : this.props.identity === 3
              ? nobgseizures
              : this.props.identity === 4
              ? nobgheadache
              : this.props.identity === 5
              ? nobggeneral
              : this.props.identity === 6
              ? cbody
              : this.props.identity === 1000
              ? cdiag
              : this.props.identity > 100 && this.props.identity < 106
              ? cpostit
              : null
          }
          alt=" "
          draggable="false"
          style={{
            width: "180px",
            position: "absolute",
            bottom: "0",
            right: "0"
          }}
        />
        <p
          style={{
            color: "grey",
            textAlign: "center",
            lineHeight: "255px",
            fontSize: "16px",
            textShadow: "2px 2px white"
          }}
        >
          {" "}
          {ask}{" "}
        </p>
      </div>
    );
  }
}
export default Board;
