import React from "react";
import mri from "../pics/Amri.png";
// import cysts from "../data/mri1_data.js";
import Supposition from "./Supposition.js";
// import AboutSupposition from "./AboutSupposition.js";
import imageCysts from "../patients/A.CTdata";

//Styles
import "../styles.css";

class Brainexercise extends React.Component {
  constructor() {
    super();
    this.state = {
      cysts: imageCysts,
      cystExerciseActive: false,
      suppositions: [],
      asking: false,
      currentType: "",
      currentUnderType: "",
      currentPlace: "",
      foundTypes: []
    };

    this.confirmSup = this.confirmSup.bind(this);
    this.handleQuestionChange = this.handleQuestionChange.bind(this);
  }

  handleQuestionChange(event) {
    const { name, value, type, checked } = event.target;
    type === "checkbox"
      ? this.setState({
          [name]: checked
        })
      : this.setState({
          [name]: value
        });
  }
  handleSuppositionClick = (event) => {
    if (event.nativeEvent.offsetX < 100 && event.nativeEvent.offsetY < 100)
      return;
    let newItem = {
      id: this.state.suppositions.length,
      x: event.nativeEvent.offsetY,
      y: event.nativeEvent.offsetX,
      type: ""
    };
    console.log(newItem);
    this.setState({
      suppositions: [...this.state.suppositions, newItem],
      asking: true
    });
  };

  undoClick = () => {
    var array = [...this.state.suppositions]; // make a separate copy of the array
    var index = this.state.suppositions.length - 1;
    if (index !== -1) {
      array.splice(index, 1);
      this.setState({ suppositions: array, asking: false });
    }
  };

  confirmSup = () => {
    this.setState({
      asking: false
    });
    let items = [...this.state.suppositions];
    let item = { ...items[this.state.suppositions.length - 1] };
    item.type = this.state.currentType;
    item.underType = this.state.currentUnderType;
    item.currentPlace = this.state.currentPlace;

    items[this.state.suppositions.length - 1] = item;
    this.setState({ suppositions: items });

    // if the location of the click corresponds approximatively to a cyst
    // location as specified in the data/mriX filem the foundAs attribute of the cyst is set accordingly
    var found = false;
    for (var a = 0; a < imageCysts.length; a++) {
      if (
        Math.abs(
          imageCysts[a].x -
            this.state.suppositions[this.state.suppositions.length - 1].y
        ) < 10 &&
        Math.abs(
          imageCysts[a].y -
            this.state.suppositions[this.state.suppositions.length - 1].x
        ) < 10
      ) {
        this.props.changeFoundAs(this.state.currentType, a);
        found = true;
      }
    }
    var sameTypeFound = false;
    for (var b = 0; b < this.state.foundTypes.length; b++) {
      if (this.state.currentType === this.state.foundTypes[b]) {
        sameTypeFound = true;
      }
    }
    if (!sameTypeFound) {
      this.setState({
        foundTypes: [...this.state.foundTypes, this.state.currentType]
      });
      this.props.makeNewArgument(this.state.currentType);
    }

    this.setState({
      currentType: "",
      currentUnderType: "",
      currentPlace: ""
    });

    if (!found) {
      this.props.updateWrongClicks();
    }
  };

  begin = () => {
    document.getElementById("start").style.display = "none";
    document.getElementById("done").style.display = "block";
    document.getElementById("image1").style.opacity = "1.0";
    this.setState({ cystExerciseActive: true });
  };

  send = () => {
    this.props.done();
    this.props.setFoundCysts(this.state.suppositions);
    this.setState({ cystExerciseActive: false });
    this.props.updateFoundCystsArgument();
    document.getElementById("DelBrutto").click();
    document.getElementById("image1").style.opacity = "0.3";
  };

  render() {
    const supps = this.state.suppositions.map((i) => (
      <Supposition
        x={i.x}
        y={i.y}
        key={i.id}
        id={i.id}
        passed_data={this.state}
        handleQuestionChange={this.props.handleQuestionChange}
        confirmSup={this.confirmSup}
      />
    ));

    return (
      <>
        {this.props.passed_data.cystExerciseDone ? (
          <h3>Your work is saved. You may return to the patient.</h3>
        ) : (
          <h3>
            Click once on every cyst or calcification you can identify on the
            following image. Use the drop-down menus which appear to choose the
            type of anomaly. Then press ok and look for more
            cysts/calcifications. <br />
            <br />
            <span style={{ fontStyle: "italic" }}>
              Click start to begin.
            </span>{" "}
          </h3>
        )}
        <div style={{ display: "flex", justifyContent: "center", marginBottom: "30px" }}>
          <button
            id="start"
            onClick={() => this.begin()}
            style={{
              fontFamily: "Geneva",
              fontSize: "23px",
              padding: "10, 10, 10, 10",
              marginTop: "10px"
            }}
            disabled={this.props.passed_data.cystExerciseDone ? true : false}
          >
            Start
          </button>
          <button
            id="done"
            onClick={() => {
              if (
                window.confirm(
                  "Once you press Done, the analysis of this image will be over and you can continue your consultation."
                )
              ) {
                this.send();
              }
            }}
            style={{
              display: "none",
              fontSize: "23px",
              fontFamily: "Geneva",
              padding: "10, 10, 10, 10",
              marginTop: "10px"
            }}
            disabled={
              this.props.passed_data.cystExerciseDone || this.state.asking
            }
          >
            Done
          </button>
          {this.props.passed_data.cystExerciseDone && (
            <button
              id="toBrutto"
              onClick={() => document.getElementById("DelBrutto").click()}
              style={{
                fontSize: "23px",
                fontFamily: "Geneva",
                padding: "10, 10, 10, 10",
                marginTop: "10px",
                marginLeft: "10px"
              }}
            >
              Classify criteria
            </button>
          )}
        </div>
        <div style={{ display: "flex", alignItems: "center" }}>
          <div
            id="container1"
            style={{ width: "449px", height: "513px", marginLeft: "30px" }}
            onClick={
              this.state.cystExerciseActive && !this.state.asking
                ? (event) => this.handleSuppositionClick(event)
                : null
            }
          >
            <img
              src={mri}
              id="image1"
              alt="Workplace"
              width="449"
              height="513"
              style={{ opacity: "0.3" }}
            />
            {supps}
          </div>

          {this.state.asking && (
            <>
              <div id="askAboutSup" style={{ marginLeft: "30px" }}>
                <select
                  value={this.state.currentType}
                  onChange={this.handleQuestionChange}
                  name="currentType"
                  style={{ fontSize: "18px", fontFamily: "Geneva" }}
                >
                  <option value="">-- Please Choose a type --</option>
                  <option
                    value="Scolex within cystic lesion"
                    style={{ color: "red" }}
                  >
                    Scolex within cystic lesion
                  </option>
                  <option
                    value="Cystic lesion without discernable scolex"
                    style={{ color: "blue" }}
                  >
                    Cystic lesion without discernable scolex
                  </option>
                  <option value="Enhancing lesion" style={{ color: "green" }}>
                    Enhancing lesion
                  </option>
                  <option
                    value="Multilobulated cyst within subarachnoid space"
                    style={{ color: "violet" }}
                  >
                    Multilobulated cyst within subarachnoid space
                  </option>
                  <option
                    value="Parenchymal brain calcification"
                    style={{ color: "#fcc203" }}
                  >
                    Parenchymal brain calcification
                  </option>
                  <option
                    value="Obstructive hydrocephalus"
                    style={{ color: "brown" }}
                  >
                    Obstructive hydrocephalus
                  </option>
                  <option
                    value="Abnormal enhancement of basal leptomeninges"
                    style={{ color: "orange" }}
                  >
                    Abnormal enhancement of basal leptomeninges
                  </option>
                </select>

                <select
                  value={this.state.currentUnderType}
                  onChange={this.handleQuestionChange}
                  name="currentUnderType"
                  style={{ fontSize: "18px", fontFamily: "Geneva" }}
                >
                  <option value="">-- Please Choose a sub-type --</option>
                  <option value="Vesicular">Vesicular</option>
                  <option value="Colloidal vesicular">
                    Colloidal vesicular
                  </option>
                  <option value="Granular nodular">Granular nodular</option>
                  <option value="Nodular calcified (inactive)">
                    Nodular calcified
                  </option>
                </select>

                <select
                  value={this.state.currentPlace}
                  onChange={this.handleQuestionChange}
                  name="currentPlace"
                  style={{ fontSize: "18px", fontFamily: "Geneva" }}
                >
                  <option value="">-- Please Choose a location --</option>
                  <option value="Parenchymal">Parenchymal</option>
                  <option value="Intraventricular">Intraventricular</option>
                  <option value="Subarachnoid space">Subarachnoid space</option>
                </select>

                <button
                  style={{
                    fontSize: "18px",
                    fontFamily: "Geneva",
                    marginLeft: "5px"
                  }}
                  onClick={this.confirmSup}
                  disabled={
                    this.state.currentType === "" ||
                    this.state.currentUnderType === "" ||
                    this.state.currentPlace === ""
                  }
                >
                  ok
                </button>
                <button
                  style={{
                    fontSize: "18px",
                    fontFamily: "Geneva",
                    marginLeft: "5px"
                  }}
                  onClick={this.undoClick}
                >
                  undo
                </button>
              </div>
            </>
          )}
          <div style={{ height: "35px" }}>
            <p id="demo" style={{ textAlign: "center" }}></p>
          </div>
        </div>
      </>
    );
  }
}

export default Brainexercise;
