import React from "react";
// import Board from "../Board"
import Back from "../Back";
import Argument from "../Argument";
import Board from "../Board";
//Styles
import "../styles.css";
class DelBrutto extends React.Component {
  render() {
    const absoluteZone = this.props.passed_data.data
      .filter((item) => item.zone === 81)
      .map((i) => (
        <Argument key={i.id} item={i} handleChange={this.props.handleChange} />
      ));
    const majorZone = this.props.passed_data.data
      .filter((item) => item.zone === 82)
      .map((i) => (
        <Argument key={i.id} item={i} handleChange={this.props.handleChange} />
      ));
    const minorZone = this.props.passed_data.data
      .filter((item) => item.zone === 83)
      .map((i) => (
        <Argument key={i.id} item={i} handleChange={this.props.handleChange} />
      ));
    return (
      <>
        <div>
          <Back func={() => document.getElementById("BrainImaging").click()} />
        </div>
        <h1>By analysing the previous CT scan, you found clues. Check out the notebook! Classify these findings according to the Del Brutto criteria. And remember, once a clue is dragged into a box, it's there to stay.</h1>
        <h2>Absolute criteria</h2>
        <Board
          className="DelBruttoBoard"
          handleDrop={this.props.handleDrop}
          identity={81}
          fill={absoluteZone}
        ></Board>
        <h2>Major criteria</h2>
        <Board
          className="DelBruttoBoard"
          handleDrop={this.props.handleDrop}
          identity={82}
          fill={majorZone}
        ></Board>
        <h2>Minor criteria</h2>
        <Board
          className="DelBruttoBoard"
          handleDrop={this.props.handleDrop}
          identity={83}
          fill={minorZone}
        ></Board>
        <div style={{ display: "flex", justifyContent: "center" }}>
          <button
            id="continue"
            onClick={() => document.getElementById("Home").click()}
            style={{
              fontFamily: "Geneva",
              fontSize: "23px",
              padding: "10px, 10px, 10px, 10px",
              marginTop: "10px"
            }}
          >
            Continue
          </button>
        </div>
      </>
    );
  }
}

export default DelBrutto;
