import React from "react";
import "./styles.css";

import greentick from "./pics/greentick.png";
import redtick from "./pics/redtick.png";
import welldone from "./pics/goldwelldone.png";

class FeedbackItem extends React.Component {
  render() {
    //this is what the user gave as an anser:
    var said = [];
    for (var a = 0; a < this.props.passed_data.data.length; a++) {
      if (this.props.passed_data.data[a].zone === this.props.boardNr) {
        said = [...said, this.props.passed_data.data[a].text];
      }
    }
    //this is what in fact did speak in favor of the keyword
    var shouldSay = [];
    for (var b = 0; b < this.props.passed_data.data.length; b++) {
      if (
        this.props.passed_data.data[b].speaksFor.includes(this.props.keyword)
      ) {
        shouldSay = [...shouldSay, this.props.passed_data.data[b].text];
      }
    }

    //this is the number of arguments which have been named by the user AND are correct
    const correctIntersection = said.filter(function (n) {
      return shouldSay.indexOf(n) !== -1;
    });

    //we now also make nice HTML elelements out of these arrays:
    const saidArg =
      said.length !== 0 ? (
        said.map((i) => (
          <p
            style={{
              color: shouldSay.includes(i) ? "green" : "grey"
            }}
            key={i}
          >
            {i}
          </p>
        ))
      ) : (
        <p style={{ color: "grey", fontStyle: "italic" }}>nothing</p>
      );

    const rightArg =
      shouldSay.length !== 0 ? (
        shouldSay.map((i) => (
          <p
            style={{
              color: said.includes(i) ? "black" : "blue"
            }}
            key={i}
          >
            {i}
          </p>
        ))
      ) : (
        <p style={{ color: "grey", fontStyle: "italic" }}>nothing</p>
      );

    const usedStyle = {
      border: "1px solid grey",
      borderRadius: "25px",
      margin: "0px 10px 10px 10px",
      padding: "5px",
      width: "35%"
    };

    //these are comments relative to arguments which should have been mentioned.

    const commentzone = this.props.passed_data.data
      .filter((item) => item.comment !== null)
      .filter((item) => shouldSay.includes(item.text))

      .map((i) => (
        <p key={i.id} style={{ marginTop: "0px", marginBottom: "0px" }}>
          {i.comment} 
        </p>
      ));

    return (
      <>
        <div style={{ display: "flex" }}>
          <h2>{this.props.title}:</h2>
          <h2
            style={{
              color:
                this.props.yourAnswer === this.props.correctAnswer
                  ? "green"
                  : "red"
            }}
          >
            {this.props.yourAnswer}
          </h2>
          {this.props.yourAnswer === this.props.correctAnswer && (
            <img
              src={greentick}
              alt="correct"
              height="20px"
              width="20px"
              style={{ marginTop: "20px" }}
            />
          )}
          {this.props.yourAnswer !== this.props.correctAnswer && (
            <>
              <img
                src={redtick}
                alt="correct"
                height="20px"
                width="20px"
                style={{ marginTop: "20px", marginLeft: "-10px" }}
              />
              <h2>It was: {this.props.correctAnswer}</h2>
            </>
          )}
        </div>
        <div
          style={{
            display: "flex",
            justifyContent: "center",
            flexWrap: "wrap"
          }}
        >
          <div className="youDid" style={usedStyle}>
            <h4>This is how you argumented:</h4>
            {saidArg}
          </div>
          <div
            className="textPart"
            style={{
              display: "flex",
              flexFlow: "column wrap",
              justifyContent: "center"
            }}
          >
            {correctIntersection.length === shouldSay.length &&
            this.props.yourAnswer === this.props.correctAnswer ? (
              <img
                src={welldone}
                alt="All correct!"
                width="170px"
                height="160px"
              />
            ) : (
              <>
                {" "}
                <p
                  style={{
                    color: "green",
                    fontSize: "15px",
                    fontFamily: "Copperplate",
                    height: "15px"
                  }}
                >
                  {correctIntersection.length} Correct Argument(s)
                </p>
                <p
                  style={{
                    color: "blue",
                    fontSize: "15px",
                    fontFamily: "Copperplate",
                    height: "15px"
                  }}
                >
                  {shouldSay.length - correctIntersection.length} Missed
                  Argument(s)
                </p>
              </>
            )}
          </div>

          <div className="youShouldHave" style={usedStyle}>
            <h4>This actually spoke for {this.props.correctAnswer}:</h4>
            {rightArg}
          </div>
        </div>
        {commentzone.length !== 0 && (
          <>
            <h4 style={{ marginBottom: "0px" }}>
              Explanation: 
            </h4>
            {commentzone}
          </>
        )}
      </>
    );
  }
}

export default FeedbackItem;
