import React from "react";

import white from "../pics/circleWhite.png";
import red from "../pics/C.red.png";
import blue from "../pics/C.blue.png";
import green from "../pics/C.green.png";
import violet from "../pics/C.violet.png";
import yellow from "../pics/C.yellow.png";
import brown from "../pics/C.brown.png";
import orange from "../pics/C.orange.png";


class Supposition extends React.Component {
  render() {
    return (
      <>
        <div
          style={{
            position: "absolute",
            top: this.props.x - 10,
            left: this.props.y - 10
          }}
        >
          <img
            alt="O"
            src={
              this.props.passed_data.suppositions[this.props.id].type === ""
                ? white
                : this.props.passed_data.suppositions[this.props.id].type ===
                  "Scolex within cystic lesion"
                ? red
                : this.props.passed_data.suppositions[this.props.id].type ===
                  "Cystic lesion without discernable scolex"
                ? blue
                : this.props.passed_data.suppositions[this.props.id].type ===
                  "Enhancing lesion"
                ? green
                : this.props.passed_data.suppositions[this.props.id].type ===
                  "Multilobulated cyst within subarachnoid space"
                ? violet
                : this.props.passed_data.suppositions[this.props.id].type ===
                  "Parenchymal brain calcification"
                ? yellow
                : this.props.passed_data.suppositions[this.props.id].type ===
                  "Obstructive hydrocephalus"
                ? brown
                : this.props.passed_data.suppositions[this.props.id].type ===
                  "Abnormal enhancement of basal leptomeninges"
                ? orange
                : white
            }
            style={{ width: "20px", height: "20px" }}
          />
        </div>
      </>
    );
  }
}

export default Supposition;
