  changeFoundAs(type, i) {
    let items = [...this.state.sups];
    let item = { ...items[i] };
    item.foundAs = type;
    items[i] = item;
    this.setState({ sups: items });
  }

  //FUNCTIONS FOR FORMS

  handleQuestionChange(event) {
    const { name, value, type, checked } = event.target;
    type === "checkbox"
      ? this.setState({
          [name]: checked
        })
      : this.setState({
          [name]: value
        });
  }

  hqc(event, i) {
    const { value } = event.target;
    let items = [...this.state.qanswers];
    let item = { ...items[i] };
    item = value;
    items[i] = item;
    this.setState({ qanswers: items });
  }

  handleChange(id, text) {
    this.setState((prevState) => (prevState.current = id));
    this.setState({ tempText: text });
  }

  //FUNCTIONS FOR DRAG AND DROP

  handleDrop(e, identity) {
    e.preventDefault();
    e.stopPropagation();

    for (var x = 0; x < this.state.disabledBoards.length; x++) {
      if (identity === this.state.disabledBoards[x]) {
        return;
      }
    }
    if (
      (identity > 100 && identity < 1000) ||
      (identity > 70 && identity < 90)
    ) {
      const zone = this.state.data
        .filter((item) => item.zone === identity)
        .map((i) => (
          <Argument
            key={i.id}
            item={i}
            handleChange={this.props.handleChange}
          />
        ));
      for (var a = 0; a < zone.length; a++) {
        if (zone[a].props.item.text === this.state.tempText) {
          return;
        }
      }
      let newItem = {
        id: this.state.data.length + 1000,
        text: this.state.tempText,
        zone: identity,
        speaksFor: [],
        comment: null
      };
      this.setState({
        data: [...this.state.data, newItem]
      });
    } else {
      this.setState((prevState) => {
        const updatedArguments = prevState.data.map((arg) => {
          if (arg.id === this.state.current) {
            arg.zone = identity;
          }
          return arg;
        });
        return {
          data: updatedArguments
        };
      });
    }
  }

  //FUNCTION to update the display of arguments
  handleDisplayArgument(identity) {
    this.setState((prevState) => {
      const updatedArguments = prevState.data.map((arg) => {
        if (arg.id === identity) {
          arg.hidden = false;
        }
        return arg;
      });
      return {
        data: updatedArguments
      };
    });
  }

  //change state when user gives an answer
  validateQuestion(i, boardToDisable) {
    let newItem = {
      id: this.state.data.length,
      text: this.state.qanswers[i],
      zone: 99,
      speaksFor:
        this.state.qanswers[i] === "Neurological signs obvious"
          ? ["non-idiopathic"]
          : this.state.qanswers[i] === "Progressive situation" ||
            this.state.qanswers[i] === "Focal neurological signs"
          ? ["CT scan"]
          : this.state.qanswers[i] === "Immunoblot positive to NCC"
          ? ["ap NCC"]
          : ["fff"],
      comment: ""
    };
    this.setState({
      data: [...this.state.data, newItem]
    });
    let items = [...this.state.qsubmitted];
    let item = { ...items[i] };
    item = true;
    items[i] = item;
    this.setState({
      qsubmitted: items,
      disabledBoards: [...this.state.disabledBoards, boardToDisable]
    });
    this.setState((prevState) => {
      return { keys: prevState.keys + 1 };
    });
    console.log(this.state.keys);
  }

