import React from "react";
import "./styles.css";
import imageCysts from "./patients/A.CTdata.js";
import annotated from "./pics/Amriwithnumbers.png";
import FeedbackItem from "./FeedbackItem.js";

import keepatrack from "./pics/keepatrack.png";

class Feedback extends React.Component {
  render() {
    window.scrollTo(0, 0);


    const boxstyle = {
      border: "1px solid blue",
      borderRadius: "15px",
      marginLeft: "15px",
      marginRight: "15px",
      marginBottom: "15px"
    };

    const askedUseful = this.props.passed_data.data
      .filter((item) => !item.hidden && item.useful)
      .map((i) => (
        <div
          style={{
            border: "1px solid grey",
            marginLeft: "20px",
            borderRadius: "15px"
          }}
        >
          <p key={i.id}> {i.about} </p>
        </div>
      ));
 

    const notAskedUseful = this.props.passed_data.data
      .filter((item) => item.hidden && item.useful)
      .map((i) => (
        <div
          style={{
            border: "1px solid grey",
            marginLeft: "20px",
            borderRadius: "15px"
          }}
        >
          <p key={i.id}> {i.about} </p>
        </div>
      ));

    const askedUseless = this.props.passed_data.data
      .filter((item) => item.about && !item.hidden && !item.useful)
      .map((i) => (
        <div
          style={{
            border: "1px solid grey",
            marginLeft: "20px",
            borderRadius: "15px"
          }}
        >
          <p key={i.id}> {i.about} </p>
        </div>
      ));

    const imagingFeedback = this.props.passed_data.sups.map((i) =>
      i.type === i.foundAs ? (
        <p key={i.id} style={{ color: "green" }}>
          {i.id + 1}: {i.type} was correctly identified.
        </p>
      ) : i.foundAs === "" ? (
        <p key={i.id} style={{ color: "grey" }}>
          {i.id + 1}: {i.type}. (You didn't identify this)
        </p>
      ) : (
        <p key={i.id} style={{ color: "red" }}>
          {i.id + 1}: {i.type}. (You erroneously identified it as a {i.foundAs})
        </p>
      )
    );

    

    return (
      <div style={{ backgroundColor: "#ffd9d9" }}>
        <h2 style={{ marginLeft: "60px" }}>
          {" "}
          Well done! Last step: read the feedback given to you in detail to see
          how you did.
          <br />
          <br />
          The <span style={{ color: "green" }}>correct arguments</span> are key
          elements you correctly used when answering the question. <br />
          <br />
          The <span style={{ color: "blue" }}>missed arguments</span> are key
          elements you missed out on when answering the question.
          <br />
          <br />
          The <span style={{ color: "grey" }}>arguments in gray</span> are not
          incorrect, but not central either.
        </h2>
        <h1 style={{ fontSize: "30px" }}>Preliminary Diagnosis</h1>
        <div
          style={boxstyle}
        >
          <h1 style={{ fontSize: "22px" }}>Dialog</h1>
          {askedUseful.length !== 0 && (
            <h2>
              You correctly asked the patient about :{" "}
              <div style={{ display: "flex" }}>{askedUseful}</div>
            </h2>
          )}
          {notAskedUseful.length !== 0 && (
            <h2>
              You should have asked the patient about :{" "}
              <div style={{ display: "flex" }}>{notAskedUseful}</div>
            </h2>
          )}
          {askedUseless.length !== 0 && (
            <h2>
              It wasn't useful though, to ask the patient about:{" "}
              <div style={{ display: "flex" }}>{askedUseless}</div>
            </h2>
          )}
        </div>

        <div
          style={boxstyle}
        >
          <h1 style={{ fontSize: "22px" }}>Classifying the epilepsy</h1>
          <FeedbackItem
            title="Presence of neurological signs"
            yourAnswer={this.props.passed_data.qanswers[0]}
            correctAnswer="Neurological signs obvious"
            passed_data={this.props.passed_data}
            boardNr={101}
            keyword="neurological sign"
          />

          <FeedbackItem
            title="Diffuse or focal impairment?"
            yourAnswer={this.props.passed_data.qanswers[1]}
            correctAnswer="Focal neurological signs"
            passed_data={this.props.passed_data}
            boardNr={102}
            keyword="focality"
          />

          <FeedbackItem
            title="Are the patient's headaches indicative of NCC?"
            yourAnswer={this.props.passed_data.qanswers[4]}
            correctAnswer="Headaches characteristic of NCC"
            passed_data={this.props.passed_data}
            boardNr={105}
            keyword="NCC headache"
          />

          <FeedbackItem
            title="Seizures pointing towards NCC?"
            yourAnswer={this.props.passed_data.qanswers[3]}
            correctAnswer="Seizures could be due to NCC"
            passed_data={this.props.passed_data}
            boardNr={104}
            keyword="NCC seizure"
          />

          <FeedbackItem
            title="Is the patient part of a risk group?"
            yourAnswer={this.props.passed_data.qanswers[2]}
            correctAnswer="Patient is part of a risk group"
            passed_data={this.props.passed_data}
            boardNr={103}
            keyword="risk group"
          />
        </div>

        <div
          style={boxstyle}
        >
          <h1 style={{ fontSize: "22px" }}>Tests: Brain imaging</h1>
          <FeedbackItem
            title="Was it right to perform a CT scan? "
            yourAnswer="performing a CT scan"
            correctAnswer="performing a CT scan"
            passed_data={this.props.passed_data}
            boardNr={71}
            keyword="CT scan"
          />
          <h1>
            There were {imageCysts.length} cysts or calcifications to be
            identified.
          </h1>
          <div
            style={{
              display: "flex",
              justifyContent: "center",
              alignItems: "center"
            }}
          >
            <img
              src={annotated}
              alt="brainImage"
              height="340px"
              width="300px"
              style={{ marginRight: "50px", marginBottom: "50px" }}
            />
            <div>{imagingFeedback}</div>
          </div>

          {this.props.passed_data.wrongClicks === 1 ? (
            <h1 style={{ color: "red" }}>
              {" "}
              One zone was clicked without being neither a cyst nor a
              calcification.
            </h1>
          ) : this.props.passed_data.wrongClicks > 1 ? (
            <h1 style={{ color: "red" }}>
              {" "}
              {this.props.passed_data.wrongClicks} zones were clicked without
              being neither a cyst nor a calcification.
            </h1>
          ) : null}
        </div>
        <div
          style={boxstyle}
        >
          <h1 style={{ fontSize: "22px" }}>Tests: Serology</h1>
          <FeedbackItem
            title="Was it right to perform serology tests? "
            yourAnswer="yes"
            correctAnswer="yes"
            passed_data={this.props.passed_data}
            boardNr={75}
            keyword="serology"
          />
        </div>
        <div
          style={boxstyle}
        >
          <h1 style={{ fontSize: "22px" }}>Tests: Coprology</h1>
        </div>
        <h1 style={{ fontSize: "30px" }}>Final Diagnosis</h1>

        <div
          style={boxstyle}
        >
          <FeedbackItem
            title="What is the patient's condition? "
            yourAnswer={this.props.passed_data.diag}
            correctAnswer="Active parenchymal NCC"
            passed_data={this.props.passed_data}
            boardNr={1000}
            keyword="Active parenchymal NCC"
          />
        </div>
        <div
          style={boxstyle}
        >
          <h1 style={{ fontSize: "22px" }}>Budget</h1>
          <h2>
            Your diagnosis cost {this.props.passed_data.spentMoney/2} and $260 was the minimum cost for the optimal consultation.
          </h2>
        </div>
        <br />

        <br />

        <br />

        <div style={{ display: "flex", justifyContent: "center" }}>
          <button
            onClick={()=>window.location.reload()}
            style={{
              padding: "8px",
              border: "1px solid black",
              textDecoration: "none",
              marginBottom: "100px",
              fontFamily: "Geneva",
              color: "black"
            }}
          >
            Start again
          </button>
        </div>
        <div
          style={{
            display: "flex",
            justifyContent: "center",
            marginTop: "-50px"
          }}
        >
          <img
            id="keepatrack"
            src={keepatrack}
            alt="Print your results!"
            onClick={() => window.print()}
          />
        </div>
        <div style={{ height: "200px" }}></div>
      </div>
    );
  }
}

export default Feedback;
