import React from "react";

import blackkey from "./pics/blackkey.png";
import greykey from "./pics/greykey.png";

class Keys extends React.Component {
  green() {
    let list = [];
    for (var a = 0; a < this.props.passed_data.keys; a++) {
      list.push(
        <img src={blackkey} alt="green key" height="40px" width="50px" key={a} />
      );
    }
    return list;
  }
  grey() {
    let list = [];
    for (var a = 0; a < 5-this.props.passed_data.keys; a++) {
      list.push(
        <img src={greykey} alt="grey key" height="40px" width="50px" key={a} />
      );
    }
    return list;
  }

  render() {
    return <div style={{ marginRight: "20px", marginTop:"10px" }}>{this.green()}{this.grey()}</div>;
  }
}

export default Keys;
